<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="组织" size="mini">
            <el-select clearable v-model="params.company_id" @focus="companyListInvok" placeholder="请选择" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="单据状态" size="mini">
            <el-select clearable v-model="params.sn_auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="录入状态" size="mini">
            <el-select clearable v-model="params.entering_tire_un" placeholder="请选择" filterable>
              <el-option label="全部" :value="0"></el-option>
              <el-option label="未录完" :value="1"></el-option>
              <el-option label="已录完" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品编号" size="mini">
            <el-input v-model="params.goods_id" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" placeholder="请选择" @focus="warehouseInvok" @change="clearAllocation">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位" size="mini">
            <el-select clearable v-model="params.allocation_id" placeholder="请选择" @focus="InventoryInvok" size="mini">
              <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据类型" size="mini">
            <el-select v-model="params.type" placeholder="请选择">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="采购入库" :value="1"></el-option>
              <el-option label="其他入库" :value="2"></el-option>
              <el-option label="三包入库" :value="3"></el-option>
              <el-option label="盘点单" :value="4"></el-option>
              <el-option label="调仓移库" :value="5"></el-option>
              <el-option label="采购退货" :value="6"></el-option>
              <el-option label="销售出库" :value="7"></el-option>
              <el-option label="三包出库" :value="8"></el-option>
              <el-option label="其他出库" :value="9"></el-option>
              <el-option label="销售退货" :value="10"></el-option>
              <el-option label="三包退货" :value="11"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="往来单位" size="mini">
            <el-input v-model="params.btype_name" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch()" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet" v-if="authority.b_export==1">
        <el-dropdown style="margin-left:10px;">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="table_container">
        <xTable v-if='tableColumn.length > 0' :ajax='{ url: "getTireTypeList", params: params }' ref='xTable' :showFooter="true" @getData='dealData' :footerMethod="footerMethod" :tableColumn='tableColumn' :xData='TableData' />
      </div>
    </div>
  </div>
</template>
<script>
import { opthionList } from "common/utils/public_function/commonUtils";
import { mapActions } from "vuex";
import { getTableHead } from "./tiremanagementIndexHead";
export default {
  name: "instorageTiremanagementTiremanagementIndex",
  data() {
    return {
      tableColumn: [],
      footerData: {},
      params: {
        entering_tire_un: 0,
        sn_auditor_status: 0,
        type: 0
      },
      footerSum: {
        subtotal_output_num: null,
        subtotal_entering_num: null,
        total_output_num: null,
        total_entering_num: null
      },
      TableData: [],
      dialogFormVisible: false,
      showDepartment: true
    };
  },
  created() {
    this.tableColumn = getTableHead(this);
    opthionList(this, window.localStorage);
    this.$set(
      this.params,
      "company_id",
      Number(localStorage.getItem("company_id"))
    );
  },
  methods: {
    ...mapActions([
      "tiremanagementSeeACT",
      "tiremanagementAddACT",
      "tiremanagementIndexACT"
    ]),
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.$refs.xTable._getData(1);
      this.footerData = {};
      this.$axMethod.getTireGoodsList(this.params).then(({ data }) => {
        if (data.code === 200) {
          this.footerData = data.data;
          setTimeout(() => {
            this.$refs.xTable._updateFooter();
          }, 50);
        }
      });
    },
    dealData(obj) {
      if (obj.data.code === 200) {
        this.$set(this, "footerSum", obj.data.data.desc);
      }
      setTimeout(() => {
        this.$refs.xTable._updateFooter();
      }, 100);
    },
    clearAllocation(val) {
      if (val != "") {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
      this.$set(this.params, "allocation_id", null);
    },
    exportOrder(scheduleType) {
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportTireTypeUrl(this.params).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    footerMethod({ columns, data }) {
      let _this = this;
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (["goods_num", "tire_num"].includes(column.property)) {
            return this.$utils.sum(data, column.property);
          }
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "总计";
          }
          if (column.property === "goods_num") {
            return this.footerData.total_goods_num;
          }
          if (column.property === "tire_num") {
            return this.footerData.total_tire_num;
          }
          if (["goods_num", "tire_num"].includes(column.property)) {
            return "加载中...";
          }
          return null;
        })
      ];
    },
    seeF(scope, row) {
      this.tiremanagementSeeACT({
        row: row
      });
      this.$router.push({
        path: "/inventory/tiremanagement/tiremanagementSee"
      });
    },
    addF(scope, row) {
      this.tiremanagementAddACT({
        row: row,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/inventory/tiremanagement/tiremanagementAdd"
      });
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    onReset(formName) {
      this.$set(this, "params", {
        type: 0,
        entering_tire_un: 0,
        sn_auditor_status: 0
      });
      this.timeValue = [];
      this.onSearch();
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "department_id", "");
    }
  },
  activated() {
    this.onSearch();
  }
};
</script>
